  margin-bottom: 0;
  font-weight: normal; }

a {
  text-decoration: none; }
  a:hover {
    text-decoration: none; }

.container {
  min-width: 320px;
  max-width: 1055px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

/* ------------ Footer bottom ------------ */
.wrapper {
