  .button:hover, .button:focus {
    background-color: #fff;
    color: #000;
    border: 2px solid #000; }
  .button:active {
    border-color: transparent; }
  .button--small {
    padding: 0.8125rem 1.25rem;
    font-size: 1.125rem;
